.contacts {
    &__list {
        display: flex;
        flex-direction: column;
        width: 100%;
        gap: 6px;
        &-item {
            width: 100%;
        }
    }
    &__link {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 6px 0;
    }
    &__link:hover {
        filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(303deg) brightness(102%) contrast(102%);
    }
    &__icon {
        width: 14px;
        -webkit-user-drag: none;
        user-select: none;
    }
}