@import '../../../sassStyles/variables';

.navbar {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 6px;
    }
    &__list-item {
        width: 100%;
    }



    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 10px;
        width: 100%;
        padding: 14px 0;
        &-icon {
            width: 26px;
            -webkit-user-drag: none;
            user-select: none;
        }
        &-label {
            display: none;
            color: $textSecondaryColor;
            // text-transform: uppercase;
            font-family: 'Maven Pro',sans-serif;
            margin-right: 10px;
        }
    }
    &__link-active .navbar__link-icon,
    &__link-active .navbar__link-label{
        filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(303deg) brightness(102%) contrast(102%);
    }
    &__link-active:after {
        content: '>';
        color: red;
        font-weight: bold;
    }
    &__link:hover .navbar__link-icon,
    &__link:hover .navbar__link-label {
        filter: invert(100%) sepia(100%) saturate(0%) hue-rotate(303deg) brightness(102%) contrast(102%);
    }
}