@import './variables.scss';
@import './mixins.scss';
@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Maven+Pro&family=Roboto&display=swap');

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,*::before, *::after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

ul, li {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  font-size: inherit;
}

button, input, textarea {
  border: none;
  outline: none;
  font-family: 'Open Sans', sans-serif;
}

button {
  cursor: pointer;
}

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  overflow-x: hidden;
  position: relative;
  color: $textPrimaryColor;
  font-family: 'Open Sans', sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 25px;
}


.section {
  width: 100%;
  height: 100%;
}

.section-title {
  font-family: 'Heebo', sans-serif;
  font-size: 50px;
  line-height: 50px;
  font-weight: 900;
}




.container {
  margin: 0 50px;
  @include smMaxBreakPoint {
    margin: 0 20px;
  }
}

.hidden-stub {
  display: none;
}