.home-section {
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__title {
        margin-bottom: 30px;
    }
    &__btn {
        padding: .8em 2.1em;
        border: 2px solid #007ced;
        border-radius: 30px;
        font-family: 'Maven Pro', sans-serif;
        box-shadow: 0 10px 10px -8px rgb(0 0 0 / 78%);
        text-transform: uppercase;
    }
    &__image {
        max-width: 800px;
    }
}







.roles__item {
    position: absolute;
    background-color: red;
    color: #fff;
    animation: fadeInOut;
    // animation-direction: alternate;
}

@keyframes fadeInOut {
    0% {
        transform: translateY(-10px);
        opacity: 0;
    }
    50% {
        transform: translateY(0);
        opacity: 1;
    }
    100% {
        transform: translateY(10px);
        opacity: 0;
    }
}